/* Books.css */

.books-container {
    padding: 20px;
  }
  
  .books-container1 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .books-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 20px;
  }
  
  .book-card {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  }
  
  .book-card:hover {
    transform: scale(1.05);
  }
  
  
  .book-title {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .book-cover {
    width: 100%;
    height: 200px;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .book-description {
    font-size: 14px;
    margin-bottom: 10px;
    /* Show only 6 lines of text and truncate with an ellipsis */
    display: -webkit-box;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  
  .view-book-btn {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .view-book-btn:hover {
    background-color: #0056b3;
  }
  