/*
=============== 
Variables
===============
*/

:root {
  /* dark shades of primary color*/
  --clr-primary-1: hsl(205, 86%, 17%);
  --clr-primary-2: hsl(205, 77%, 27%);
  --clr-primary-3: hsl(205, 72%, 37%);
  --clr-primary-4: hsl(205, 63%, 48%);
  /* primary/main color */
  --clr-primary-5: hsl(205, 78%, 60%);
  /* lighter shades of primary color */
  --clr-primary-6: hsl(205, 89%, 70%);
  --clr-primary-7: hsl(205, 90%, 76%);
  --clr-primary-8: hsl(205, 86%, 81%);
  --clr-primary-9: hsl(205, 90%, 88%);
  --clr-primary-10: hsl(205, 100%, 96%);
  /* darkest grey - used for headings */
  --clr-grey-1: hsl(209, 61%, 16%);
  --clr-grey-2: hsl(211, 39%, 23%);
  --clr-grey-3: hsl(209, 34%, 30%);
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(210, 22%, 49%);
  --clr-grey-6: hsl(209, 23%, 60%);
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
  --clr-white: #fff;
  --clr-red-dark: hsl(360, 67%, 44%);
  --clr-red-light: hsl(360, 71%, 66%);
  --clr-green-dark: hsl(125, 67%, 44%);
  --clr-green-light: hsl(125, 71%, 66%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 620px;
}
/*
=============== 
Global Styles
===============
*/

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: var(--clr-grey-10);
  color: var(--clr-grey-1);
  line-height: 1.5;
  font-size: 0.875rem;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}
h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 0.875rem;
}
p {
  margin-bottom: 1.25rem;
  color: var(--clr-grey-5);
}
@media screen and (min-width: 800px) {
  h1 {
    font-size: 4rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}
* {
  border: 1px solid blac;
}
/*  global classes */
#main {
  width: 90vw;
  margin: 2rem auto;
  max-width: var(--max-width);
}
#read h3{
text-align:center;
}

#read {
  width: 90vw;
  margin: 2rem auto;
  max-width: var(--max-width);
}
#title {
  padding: 1.4rem;
  text-align: center;
  margin: 2rem;
}
#img-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}
#image {
  max-width: 30vw;
  height: auto;
  object-fit: cover;
  box-shadow: 0px 5px 15px black, 0px -1px 15px black;
  margin-top:100px;
}
#img-caption {
  color: var(--clr-grey-4);
  padding: 1rem;
}

#tribute-info {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 1fr;
  text-align: center;
  background-color: var(--clr-grey-9);
  /* gap: 5px; */
  padding: 5px;
}
#tribute-info > * {
  background-color: #fff;
}
#tribute-info:hover {
  box-shadow: 0px 5px 15px black, 0px -1px 15px black;
  z-index: 1;
  transition: box-shadow 1s ease;
  cursor: pointer;
  z-index: 2;
}
/* Add this CSS to your existing CSS file or create a new one */

#tribute-info img {
  display: block;
  margin: auto;
  transition: transform 0.3s ease; /* Add transition for smooth animation */
}

#tribute-info img:hover {
  transform: scale(1.1);
  cursor: pointer;
}


.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1rem;
  font-weight:bold;
}
#a-link {
  padding: 2rem;
}
#tribute-link {
  font-size: 1rem;
}
@media screen and (min-width: 992px) {
  #main {
    width: 95vw;
  }
  #title {
    padding: 2rem;
  }
  #tribute-info {
    grid-template-columns: 2fr 6fr;
    grid-template-rows: auto;
  }
  #tribute-info img {
    padding: 1rem;
  }
  #a-link {
    display: flex;
    justify-content: center;
    padding: 2rem;
  }
}
/* styles.css */
@keyframes zoomOut {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.2); /* Adjust the scale as needed */
  }
}

.bg-image {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('https://static.businessworld.in/article/article_extra_large_image/1459317411_cadnWt_Group-of-People-Waving-Indian-Flags-in-Back-Lit-shutterstock_870.jpg'); /* Provided image URL */
  background-size: cover;
  background-position: center;
  animation: zoomOut 10s ease-in-out; /* Adjust the animation duration as needed */
  z-index: -1; /* Ensure the background image is behind other content */
  opacity: 0.5; /* Adjust the opacity as needed */
}

