/* Footer.css */

.footer {
    background-color: #333;
    color: #fff;
    padding: 50px 0;
  }
  
  .footer-content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    text-align:center;
  }
  
  .footer-section {
    flex: 1 1 300px;
    margin-bottom: 30px;
  }
  
  .footer-section p {
    font-size: 25px;
    margin-bottom: 15px;
  }
  
  .footer-section h3 {
    font-size: 33px;
    margin-bottom: 15px;
  }
  
  .footer-section ul {
    list-style: none;
    padding: 0;
  }
  
  .footer-section ul li  {
    margin-bottom: 10px;
    font-size: 25px;
  }
  
  .footer-section ul li a {
    color: #fff;
    text-decoration: none;
  }
  
  .footer-section ul li a:hover {
    color: #ccc;
  }
  
  .copyright {
    text-align: center;
    margin-top: 50px;
  }
  .copyright p{
    color: #fff;
  }
  