/* TributeInfo.css */

.tribute-info-r {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  backgroung-color:white;
  }
  
  .details {
    margin-bottom: 2rem;
  }
  
  .details h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  
  .details p {
    font-size: 1.2rem;
    line-height: 1.6;
  }
  
  .gallery {
    margin-bottom: 2rem;
  }
  
  .gallery h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  
  .image-container {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
  }
  
  .image-container img {
    width: 200px;
    height: auto;
    border-radius: 5px;
  }
  
  .video-section {
    margin-bottom: 2rem;
  }
  
  .video-section h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  
  .video-section iframe {
    width: 80%;
    height: 400px;
    border: none;
    border-radius: 5px;
  }
  
  .tribute-info-container1 {
    max-width: 1300px;
    margin: 0 auto;
    padding: 20px;
    background: url('data:image/jpeg;base64,');
    font-size: 24px;
 
  }
  h2{
    text-align:center;
    color:black;
    font-size: 50px;
    font-weight:bold;
  }
  .details {
    margin-bottom: 30px;
    color:black;
   font-weight:bold;
  }
  
  .details h3 {
    font-size: 26px;
    margin-bottom: 10px;
    color:black;
    font-weight:900;
  }
  
  .details p {
    font-size: 22px;
    line-height: 1.6;
    color:black;
  }
  
  .gallery {
    margin-bottom: 30px;
  }
  
  .gallery h3 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .image-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
  }
  
  .image-container img {
    width: 100%;
    border-radius: 5px;
  }
  
 
  