/* Awards.css */
.awards-container {
    padding: 20px;
  }
  h1{
    text-align:center;
  }
  .awards-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 20px;
  }
  
  .award-card {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .award-image {
    width: 100%;
    height: auto;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    transition: transform 0.3s ease; /* Add transition property */
  }
  
  /* Add hover effect */
  .award-image:hover {
    transform: scale(0.9); /* Zoom out to 90% */
  }
  
  
  .award-details {
    padding: 20px;
  }
  
  .award-details h2 {
    font-size: 22px;
    margin-bottom: 10px;
  }
  
  .award-details p {
    font-size: 17px;
    margin-bottom: 10px;
    color:black;
   
  }
  