.navbar {
    background-color: #333;
    color: #fff;
    padding: 1rem 0;
    width:100%;
    position:fixed;
    height:70px;
    text-align: center;
  }
  .navbar h1 {
    color:white;
    text-align: center;
    margin-left:250px;
  } 
  .container {
    display: flex;
    margin-top:150px;
    justify-content: space-between;
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 1rem;
  }
  
  .navbar-logo {
    font-size: 1.5rem;
  }
  
  .navbar-links {
    list-style: none;
    display: flex;
    align-items: center;
  }
  
  .navbar-item {
    margin-right: 1rem;
  }
  
  .navbar-item:last-child {
    margin-right: 0;
  }
  
  .navbar-item a {
    color: #fff;
    text-decoration: none;
    padding: 0.5rem;
    transition: color 0.3s ease;
  }
  
  .navbar-item a:hover {
    color: #ffd700; /* Change color on hover */
  }
