/* Contribution.css */

/* Contribution container */
.contribution-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  
  /* Contribution title */
  .contribution-container h1 {
    text-align: center;
    font-size: 2rem;
    color: #333;
    margin-bottom: 20px;
  }
  
  /* Contribution grid */
  .contribution-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
  }
  
  /* Contribution card */
  .contribution-card {
    width: 350px;
     background: url('data:image/jpeg;base64,');
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out;
  }
  
  .contribution-card:hover {
    transform: scale(1.05);
  }
  
  /* Contribution image */
  .contribution-image {
    width: 100%;
    height: 300px;
    display: block;
  }
  
  /* Contribution details */
  .contribution-details {
    padding: 20px;
  }
  
  /* Contribution title */
  .contribution-details h2 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  

  .contribution-container h1 {
    font-size: 50px;
    margin-bottom: 10px;
  }
  
  /* Contribution description */
  .contribution-details p {
    font-size: 1rem;
    color: #666;
    line-height: 1.5;
  }
  